import React, { useRef } from "react";

import { arrayRearrange } from "shared/helpers/dataStructure";
import { toNumber } from "shared/helpers/number";
import { T_ListItemMarkupOptions } from "./types";
import "./styles.scss";

export default function AnalogClock(props: {
  hh: number;
  mm: number;
  ss: number;
}) {
  const hh = props.hh === totalDigitalHours ? 0 : props.hh;
  const mm = props.mm === totalMinutes ? 0 : props.mm;
  const ss = props.ss === totalSeconds ? 0 : props.ss;
  const hhHandInitialRotation = hh * perHourRotation;
  const mmHandInitialRotation = mm * perMinuteRotation;
  const ssHandInitialRotation = ss * perSecondRotation;

  const hhRef = useRef<HTMLDivElement>(null);
  const mmRef = useRef<HTMLDivElement>(null);
  const ssRef = useRef<HTMLDivElement>(null);

  hhRef.current?.animate(
    [
      // { transform: `rotate(${hhHandInitialRotation}deg)`, offset: 0 },
      { transform: `rotate(${360 + hhHandInitialRotation}deg)`, offset: 1 }
    ],
    { duration: 43200, iterations: Infinity, easing: "linear", delay: 0 }
  );
  mmRef.current?.animate(
    [
      // { transform: `rotate(${mmHandInitialRotation}deg)`, offset: 0 },
      { transform: `rotate(${360 + mmHandInitialRotation}deg)`, offset: 1 }
    ],
    // options
    { duration: 3600, iterations: Infinity, easing: "linear", delay: 0 }
  );
  ssRef.current?.animate(
    [
      // { transform: `rotate(${ssHandInitialRotation}deg)`, offset: 0 },
      { transform: `rotate(${360 + ssHandInitialRotation}deg)`, offset: 1 }
    ],
    // options
    { duration: 60, iterations: Infinity, easing: "linear", delay: 0 }
  );
  return (
    <div className="AnalogClock">
      <div id="watch">
        <div className="frame-face"></div>
        <ul className="minute-marks">
          {getNListItemsMarkup(totalMinutesMark, { doNotRenderIndex: true })}
        </ul>
        {/* {getDigitalClockMarkup(hh, mm, ss)} */}
        <ul className="digits">
          {getNListItemsMarkup(totalHours, { naturalNumbering: true })}
        </ul>
        <div
          ref={hhRef}
          className="hours-hand"
          // style={{ transform: `rotate(${hhHandInitialRotation}deg)` }}
        />
        <div
          ref={mmRef}
          className="minutes-hand"
          // style={{ transform: `rotate(${mmHandInitialRotation}deg)` }}
        />
        <div
          ref={ssRef}
          className="seconds-hand"
          // style={{ transform: `rotate(${ssHandInitialRotation}deg)` }}
        />
      </div>
    </div>
  );
}

function getDigitalClockMarkup(hh: number, mm: number, ss: number) {
  const hhItems = arrayRearrange(
    getNListItemsMarkup(totalDigitalHours, {
      zeroPadIndex: true
    }),
    toNumber(hh)
  );

  const mmItems = arrayRearrange(
    getNListItemsMarkup(totalMinutes, { zeroPadIndex: true }),
    toNumber(mm)
  );
  const ssItems = arrayRearrange(
    getNListItemsMarkup(totalSeconds, { zeroPadIndex: true }),
    toNumber(ss)
  );

  return (
    <div className="digital-wrap">
      <ul className="digit-hours">{hhItems}</ul>
      <ul className="digit-minutes">{mmItems}</ul>
      <ul className="digit-seconds">{ssItems}</ul>
    </div>
  );
}

function getNListItemsMarkup(n: number, options?: T_ListItemMarkupOptions) {
  return [...Array(n)].map((e, index) => {
    let valueToRender: string | number = index;
    if (options?.naturalNumbering) {
      valueToRender = index + 1;
    }
    if (options?.zeroPadIndex) {
      valueToRender = String(valueToRender).padStart(2, "0");
    }
    if (options?.doNotRenderIndex) {
      valueToRender = "";
    }

    return <li key={index}>{valueToRender}</li>;
  });
}

const totalSeconds = 60;
const totalMinutes = 60;
const totalHours = 12;
const totalDigitalHours = 24;
const totalMinutesMark = 48; // 60 - <count where the actual digits are displayed instead of marks>
const perMinuteRotation = 360 / 60;
const perSecondRotation = 360 / 60;
const perHourRotation = 360 / 12;
